import React, { useState, useEffect } from "react";
import axios from "axios";
import { useRouter } from "next/router";
import AddFridgeItemForm from "../components/AddFridgeItemForm";
import FridgeItem from "../components/FridgeItem";
import "../styles/globals.css";

const Home = () => {
  const [items, setItems] = useState([]);
  const router = useRouter();

  useEffect(() => {
    fetchItems();
  }, []);

  const fetchItems = async () => {
    try {
      const response = await axios.get("http://127.0.0.1:8000/fridge_items/");
      setItems(response.data);
    } catch (error) {
      console.error("Error fetching fridge items:", error);
    }
  };

  const handleRecipeSearch = () => {
    const ingredients = items.map((item) => item.name).join(",");
    router.push(`/recipes?ingredients=${ingredients}`);
  };

  return (
    <div className="container">
      <h1 className="title">ÂÜ∑ËîµÂ∫´„ÅÆÁÆ°ÁêÜ</h1>
      <AddFridgeItemForm setItems={setItems} />
      <div className="fridge-items">
        {items.map((item) => (
          <FridgeItem key={item.id} item={item} setItems={setItems} />
        ))}
      </div>
      <button onClick={handleRecipeSearch} className="search-button">„É¨„Ç∑„Éî„ÇíÊ§úÁ¥¢</button>
    </div>
  );
};

export default Home;
